<template>
    <div class="message-list">
        <div class="search" :class="{active:searchType}">
            <div class="search-box">
                <el-input :placeholder="$L('输入标题')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
            <div class="bbx-font bbx-icon-close mainFontColor" @click="showSearch(false)"></div>
        </div>
        <div class="search-status">
            <el-select class="search" v-model="messageType" size="small"
                       @change="changeTypeFunc">
                <el-option
                        v-for="item in messageTypelist"
                        :key="item.type"
                        :label="item.label"
                        :value="item.type">
                </el-option>
            </el-select>
        </div>

        <div class="content" v-if="tableData && tableData.hasOwnProperty('list')">
            <div class="content-box"  v-loading="loading">
                <el-table
                        :data="tableData.list"
                        ref="multipleTable"
                        style="width: 100%" class="table"
                        :empty-text="$L('暂无数据')"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="40" v-if="editSelect">
                    </el-table-column>
                    <el-table-column
                            :label="$L('标题')"
                            align="left">
                        <div slot-scope="scope">
                            <!-- 长按不能单词点击@touchstart.prevent="touchIn(scope.row.id)" @touchend.prevent="cleartime(scope.row.id)"-->
                            <p class="title" v-html="scope.row.title"  @click="getInfoFunc(scope.row.id)"> </p>
                            <p class="font-grey">{{ scope.row.createTime }}</p>
                            <p :class="statusFunc(scope.row.status).color">{{ statusFunc(scope.row.status).text }}</p>
                        </div>
                    </el-table-column>
                    <el-table-column
                            width="10">
                    </el-table-column>
                </el-table>
                <div class="page" v-if="tableData.allNum>pageSize">
                    <el-pagination
                            small
                            background
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :page-size="pageSize"
                            layout="prev, pager, next"
                            :total="tableData.allNum">
                    </el-pagination>
                </div>
            </div>

            <div class="botton-btn" v-if="editSelect">
                <div class="left">
                    <el-checkbox v-model="checkAll" @change="handleCheckAllChange(tableData.list)">{{ $L('全选') }}</el-checkbox>
                </div>
                <div class="right">
                    <el-button class="btn" type="primary" :disabled=disabled @click="delMsgFunc()" >{{ $L('删除') }}</el-button>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "messageList",
        data () {
            return {
                loading:false,
                searchType: false,
                inputValue:'',
                messageType:0,
                messageTypelist:[
                    {
                        type:0,
                        label:this.$L('全部消息')
                    },
                    {
                        type:2,
                        label:this.$L('未读')
                    },
                    {
                        type:1,
                        label:this.$L('已读')
                    },
                ],
                disabled:true,
                allDisabled:true,
                tableData:{},
                currentPage:1,
                pageSize:10,
                msgArr:[],
                allArr:[],
                editSelect:false,
                checkAll:false,
                isIndeterminate:true
            }
        },
        computed: {
            //用于读取和设置顶部按钮
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 执行设置顶部按钮
            this.setupTopBtn();
            this.showSearch(false);
            next();
        },
        created() {
            //创建当前页面时执行设置顶部按钮
            this.setupTopBtn();
            this.getData();
        },
        beforeDestroy() {
            //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
            this.mobileTopBtn = null
        },
        methods : {
            setupTopBtn() {
                this.mobileTopBtn = [
                    {
                        text: this.$L('编辑'),
                        size: '14px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.edit();
                        }
                    },
                    {
                        icon: 'bbx-icon-sousuo',
                        size: '24px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.showSearch(true);
                        }
                    }
                ];
            },
            showSearch(type) {
                if (type) {
                    //显示搜索框内容
                    this.searchType = true;
                } else {
                    //隐藏搜索框内容
                    this.searchType = false
                }
            },
            //编辑
            edit(){
                this.editSelect = !this.editSelect;
                if(this.editSelect){
                    this.mobileTopBtn[0].text = this.$L('取消');
                }else{
                    this.mobileTopBtn[0].text = this.$L('编辑');
                }
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    type : _self.messageType,
                    keyword : _self.inputValue
                };
                _self.$api('getMsgList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                    });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            getInfoFunc(id){
                this.$router.push({name: 'messageInfo',params:{id:id}});
            },
            handleSelectionChange(val){
                if(val.length > 0){
                    this.disabled = false;
                }else{
                    this.disabled = true;
                }
                let arr = [];
                for (let i in val){
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
            },
            statusFunc(value){
                let x;
                switch(value){
                    case 0:
                        x = {text:this.$L('未读'),color:'font-red'};
                        break;
                    default:
                        x = {text:this.$L('已读'),color:'font-green'};
                        break;
                }
                return x;
            },
            changeTypeFunc(val){
                this.messageType = val;
                this.getData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.checkAll = false;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            delMsgFunc(id){

                const _self = this;
                if (!id && (Array.isArray(this.msgArr) && this.msgArr.length === 0)) {

                    //未知错误
                    _self.$confirm(_self.$L('您还没有选中消息，请勾选会员信息再做此操作'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'warning',
                        distinguishCancelAndClose: true,
                        closeOnClickModal: false,
                        center: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                    return ;
                }

                let groupId;

                if (id) {
                    groupId = id;
                } else {
                    groupId = this.msgArr;
                }

                let arr = {
                    messageIds : groupId,
                }
                console.log(arr);
                _self.$confirm(_self.$L('此操作将永久删除该消息, 是否继续?'),_self.$L( '提示'), {
                    confirmButtonText:_self.$L( '确定'),
                    cancelButtonText:_self.$L( '取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('deleteMessage', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.loading = false;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('删除成功!')
                            });
                            _self.getData();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }).catch(() => {

                });
            },
            handleCheckAllChange(rows){
                if (rows) {
                    rows.forEach(rows => {
                        this.$refs.multipleTable.toggleRowSelection(rows, !this.allSelect);
                    })
                    this.allSelect = !this.allSelect;

                    let arr = [];
                    if (this.allSelect) {
                        for (let i in rows){
                            arr[i] = rows[i].id;
                        }
                        this.msgArr = arr;
                        this.disabled = false;
                    } else {
                        this.msgArr = arr;
                        this.disabled = true;
                    }
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../assets/css/common/lib.scss';
    .message-list {
        background: #eff1f5;

        .search {
            position: relative;
            width: 100%;
            height: 0;
            line-height: 50px;
            text-align: center;
            overflow: hidden;
            @include diy-animation-show(height);
            border-bottom: 1px solid #F2F6FC;

            &.active {
                height: 50px;
                padding: 0 10px;
                background: #fff;
            }

            .bbx-font {
                position: absolute;
                width: 50px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                top: 0;
                right: 0;
                font-size: 20px;
            }
            .search-box{
                width: calc(100% - 40px);
                /deep/ .el-input__inner{
                    padding: 0 10px;
                }
                /deep/ .el-input-group__append{
                    padding: 0 12px;
                    background: #fff;
                    color: #333;
                    font-size: 16px;
                }
            }
        }
        .search-status{
            height: 52px;
            padding: 0 10px;
            border-bottom: 1px solid #F2F6FC;
            background: #fff;
            .search{
                height: inherit;
                width: 30%;
                border-bottom:0;
                vertical-align: top;
                /deep/ .el-input__inner{
                    padding: 0 10px;
                }
            }
            .edit{
                height: inherit;
                vertical-align: top;
                float: right;
                width: 50px;
            }
        }
        .content{
            width: 100%;
            .content-box{
                margin: 10px 0;
                margin-bottom: 55px;
                .table{
                    background: #fff;
                    border-bottom: 1px solid #F2F6FC;
                    padding: 0 10px;
                    font-size: 12px;
                    /deep/ .el-table__header-wrapper{
                        display: none;
                    }
                    .title{
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        overflow: hidden;
                    }
                    .font-red {
                        color: #ff0000;
                    }
                    .font-green {
                        color: #00bf00;
                    }
                    .font-grey {
                        color: #999;
                    }
                }
            }
            .page{
                background: #fff;
                padding: 20px 0;
                text-align: center;
                margin-bottom: 50px;
            }
            .botton-btn{
                position: fixed;
                bottom: 0;
                width: 100%;
                height: 50px;
                background: #fff;
                z-index: 100;
                border-top: 1px solid #F2F6FC;
                .left{
                    display: inline-block;
                    line-height: 50px;
                    float: left;
                    padding-left: 10px;
                }
                .right{
                    display: inline-block;
                    height: 50px;
                    line-height: 50px;
                    float: right;
                    .btn{
                        height: inherit;
                        width: 120px;
                    }
                }
            }
        }

    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>